import React from 'react';
import {Route} from 'react-router-dom';

const AppRoute = ({
                      component: Component,
                      layout: Layout,
                      componentProps,
                      ...rest
                  }) => {

    Layout = (Layout === undefined) ? props => (<>{props.children}</>) : Layout;

    return (
        <Route
            {...rest}
            render={props => (
                <Layout>
                    <Component {...props} {...componentProps} />
                </Layout>
            )}/>
    );
}

export default AppRoute;